.copyright {
  display: flex;
  align-items: center;
  width: 130px;
  height: 34px;
  margin: 0 auto;

  text-align: right;
    padding-right: 44px;
}

.copyright__text {
  .font(14px;18px);
  font-weight: 400;
}

.copyright__link {
  .font(0;0);
}

@media (min-width: @tablet-width) {
  .copyright {
    margin-right: 0;
  }
}
